            <p class="stat-desc">ÏÜåÎ∞©Ï≤≠ Ïó∞Í∞Ñ ÏÇ∞ÏïÖÏÇ¨Í≥† ÏûêÎ£åÏ§ë ÏùºÎ∂Ä (2023ÎÖÑ Îßê Í∏∞Ï§Ä)</p>
