        <div class="hot-posts-list">
          <div class="hot-post-card" v-for="i in 3" :key="i">
            <img src="/images/climbing.png" alt="Í≤åÏãúÎ¨º Ïù¥ÎØ∏ÏßÄ" class="hot-post-image" />
