    <div class="toggle-switch-container2" style="z-index: 10;"> <!-- Í∏∞Ï°¥ z-indexÎ•º ÎÇÆÏ∂∞ ÏÑ§Ï†ï -->
        <div class="toggle-switch2">
          <img src="/images/ÎÇ¥ÏúÑÏπò.png" alt="" style="width: 13px; height: auto; margin-right: 5px;"> ÎÇ¥ ÏúÑÏπò
        </div>
        <div class="toggle-switch2" >
          <img src="/images/SOS.png" alt="" style="width: 13px; height: auto; margin-right: 5px;"> SOS
