        <div class="toggle-switch2" style=" font-size: 12px; width: 100px;">
          <img src="/images/Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏.png" alt="" style="width: 13px; height: auto; margin-right: 5px;"> Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏
        </div>
      </div>
   
    <div v-if="!isSOSActive" class="info-panel">
      <div  id="overline"></div>
