      <div style="position: relative; text-align: center;">
      
        <div style="position: relative; top: 130px; font-size: 12px; color: white;">ÌòÑÏû¨ ÏúÑÏπòÍ∞Ä Ï£ºÎ≥Ä ÏÇ¨ÎûåÎì§( 300M Ïù¥ÎÇ¥ )ÏóêÍ≤å ÏïåÎ¶ºÏúºÎ°ú Ï†ÑÏÜ°Îê©ÎãàÎã§.</div>
      </div>
      <div id="wrap">
          <button v-if="!isSOSActive" class="overlay-button" @click="handleSendNotification">SOS</button>
          <div v-if="!isSOSActive" class="wave -one"></div>
