      </div>
    </div>

    <div v-else class="info-panel">
      <h3 style="position:relative; text-align: center; top: 25px">Í∏¥Í∏â ÎèÑÏõÄ ÏöîÏ≤≠</h3>
      <div style="font-size: 20px; font-weight: bold; margin-top: 25px; text-align: center; color: white;">SOS ÏïåÎ¶º  <span style="color: #38b64a;">Ï†ÑÏÜ° ÏôÑÎ£å</span>(300M)</div>

        <div class="button-container">
        <div class="button-text-container">
          <button class="action-button" @click="reSendSOS">
            <img src="/images/Î¶¨ÏΩú.png" alt="Îã§Ïãú Î≥¥ÎÇ¥Í∏∞">
          </button>
          <div class="description-text" style="font-size: 10px; margin-top: 5px; color: white;">ÏïåÎ¶º Î≥¥ÎÇ¥Í∏∞</div>
        </div>
        <div class="button-text-container">
          <button class="action-button" @click="callEmergency">
            <img src="/images/119.png" alt="119">
          </button>
          <div class="description-text" style="font-size: 10px; margin-top: 5px; color: white;">119 Ï†ÑÌôî</div>
        </div>
        <div class="button-text-container">
          <button class="action-button" @click="findNearestPoint">
            <img src="/images/Íµ≠Í∞ÄÌëúÏßÄÌåê.png" alt="Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏">
          </button>
          <div class="description-text" style="font-size: 10px; margin-top: 5px; color: white;">
            Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏
          </div>
        </div>
         <!-- Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏ Î™®Îã¨ -->
         <div v-if="isPointInfoVisible" class="info-display">
          <h3>Í∞ÄÏû• Í∞ÄÍπåÏö¥ Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏</h3>
          <p>ÏßÄÏ†êÎ≤àÌò∏: {{ nearestPoint?.label || 'Ïïå Ïàò ÏóÜÏùå' }}</p>
          <p>ÏúÑÎèÑ: {{ nearestPoint?.latitude }}</p>
          <p>Í≤ΩÎèÑ: {{ nearestPoint?.longitude }}</p>
          <p>Í±∞Î¶¨: {{ nearestPoint?.distance?.toFixed(2) || 0 }} km</p>
          <p>Î∞©Ìñ•: {{ nearestPoint?.direction || 'Ïïå Ïàò ÏóÜÏùå' }}</p>
          <button @click="closePointInfo" class="close-button">Îã´Í∏∞</button>
        </div>
      </div>
    </div>
    <MobileFooterView4 class="footer"></MobileFooterView4>
  </div>

</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, computed} from 'vue';
import axios from 'axios';
import MobileFooterView4 from '@/components/MobileFooterView4.vue';

const messages = ref([]);
const userId = ref(localStorage.getItem('userId') || '');
let socket = null;
let reconnectInterval = null;
const connectionError = ref('');
const successMessage = ref('');
let isConnecting = false;
const isModalOpenForSOS = ref(false); // SOS Î™®Îã¨ ÏÉÅÌÉú
const isSOSActive = ref(false);

const isPointInfoVisible = ref(false); // Ï†ïÎ≥¥ ÌëúÏãú Ïó¨Î∂Ä
const nearestPoint = ref(null);

// ÏÇ¨Ïö©Ïûê ÏúÑÏπò
const userLocation = ref({ latitude: null, longitude: null });

// SOS ÏïåÎ¶º ÏúÑÏπò
const sosNotificationLocation = ref({ latitude: null, longitude: null });

// ÏßÄÎèÑ Í¥ÄÎ†® Î≥ÄÏàò
const mapContainer = ref(null);
let map = null;
let userMarkers = {}; // Í∞Å userIdÏóê Ìï¥ÎãπÌïòÎäî ÎßàÏª§ Ï†ÄÏû• Í∞ùÏ≤¥
let sosMarkers = []; // SOS ÎßàÏª§ Î∞∞Ïó¥

let autoCenterEnabled = true;

const filteredMessages = computed(() => messages.value.filter(msg => msg.text !== userId.value));

// Î∞©Ìñ• Í≥ÑÏÇ∞ Ìï®Ïàò
const calculateDirection = (lat1, lon1, lat2, lon2) => {
  const dLat = lat2 - lat1;
  const dLon = lon2 - lon1;

  if (Math.abs(dLat) > Math.abs(dLon)) {
    return dLat > 0 ? "ÎÇ®Ï™Ω" : "Î∂ÅÏ™Ω";
  } else {
    return dLon > 0 ? "ÏÑúÏ™Ω" : "ÎèôÏ™Ω";
  }
};

// Í±∞Î¶¨ Í≥ÑÏÇ∞ Ìï®Ïàò (ÏúÑÎèÑ, Í≤ΩÎèÑÎ°ú Í±∞Î¶¨ Íµ¨ÌïòÍ∏∞)
const calculateDistance = (lat1, lon1, lat2, lon2) => {
  const R = 6371; // ÏßÄÍµ¨ Î∞òÏßÄÎ¶Ñ (km)
  const dLat = (lat2 - lat1) * (Math.PI / 180);
  const dLon = (lon2 - lon1) * (Math.PI / 180);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(lat1 * (Math.PI / 180)) *
      Math.cos(lat2 * (Math.PI / 180)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return R * c; // Í±∞Î¶¨ Î∞òÌôò (km)
};

// Í∞ÄÏû• Í∞ÄÍπåÏö¥ ÏßÄÏ†ê Ï∞æÍ∏∞
const findNearestPoint = async () => {
  if (!userLocation.value.latitude || !userLocation.value.longitude) {
    console.warn("ÏÇ¨Ïö©ÏûêÏùò ÏúÑÏπòÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§:", userLocation.value);
    alert("ÌòÑÏû¨ ÏúÑÏπòÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
    return;
  }

    // GeoJSON Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
    const response = await axios.get(`${window.location.origin}/data/Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏2.geojson`);
    const geojsonData = response.data;

    if (!geojsonData || !geojsonData.features || geojsonData.features.length === 0) {
      alert("Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏ Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.");
      return;
    }

    let closestPoint = null;
    let minDistance = Infinity;

    geojsonData.features.forEach((feature) => {
      const pointLatitude = feature.properties.YÏ¢åÌëú; // YÏ¢åÌëú (ÏúÑÎèÑ)
      const pointLongitude = feature.properties.XÏ¢åÌëú; // XÏ¢åÌëú (Í≤ΩÎèÑ)
      const pointLabel = feature.properties.ÏßÄÏ†êÎ≤àÌò∏; // ÏßÄÏ†êÎ≤àÌò∏ (Îã§ÏÇ¨52273869 ÌòïÏãù)

      const distance = calculateDistance(
        userLocation.value.latitude,
        userLocation.value.longitude,
        pointLatitude,
        pointLongitude
      );

      console.log(
        `ÏßÄÏ†ê: ${pointLabel}, Í±∞Î¶¨: ${distance.toFixed(2)} km, ÌòÑÏû¨ ÏµúÏÜå Í±∞Î¶¨: ${minDistance}`
      );

      if (distance < minDistance) {
        minDistance = distance;
        closestPoint = {
          label: pointLabel,
          latitude: pointLatitude,
          longitude: pointLongitude,
          distance,
          direction: calculateDirection(
            userLocation.value.latitude,
            userLocation.value.longitude,
            pointLatitude,
            pointLongitude
          ),
        };
      }
    });

    if (closestPoint) {
    nearestPoint.value = closestPoint; // Í∞ÄÏû• Í∞ÄÍπåÏö¥ ÏßÄÏ†ê Ï†ïÎ≥¥ Ï†ÄÏû•
    console.log("Í∞ÄÏû• Í∞ÄÍπåÏö¥ ÏßÄÏ†ê Ï†ïÎ≥¥:", nearestPoint.value); // ÎîîÎ≤ÑÍ∑∏ Î°úÍ∑∏
    isPointInfoVisible.value = true; // ÌôîÎ©¥Ïóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ï
    console.log("isPointInfoVisible ÏÉÅÌÉú:", isPointInfoVisible.value); // ÎîîÎ≤ÑÍ∑∏ Î°úÍ∑∏
  } else {
    alert("Í∞ÄÍπåÏö¥ Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
  }
};

const closePointInfo = () => {
  isPointInfoVisible.value = false; // Ï†ïÎ≥¥ Ïà®Í∏∞Í∏∞
};

// SOS Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú ÏúÑÏπò Ï†ïÎ≥¥ ÏóÖÎç∞Ïù¥Ìä∏
const handleSendNotification = async () => {
  isSOSActive.value = true; // SOS ÌôúÏÑ±Ìôî
  isModalOpenForSOS.value = true; // Î™®Îã¨ ÌëúÏãú

   // SOS ÏïåÎ¶º ÏúÑÏπòÎ•º ÏóÖÎç∞Ïù¥Ìä∏
  sosNotificationLocation.value.latitude = userLocation.value.latitude;
  sosNotificationLocation.value.longitude = userLocation.value.longitude;
  
  try {
    // SOS ÏïåÎ¶º Ï†ÑÏÜ°
    await axios.post("http://172.30.1.53:9000/api/sendNotificationToAll", {
      userId: localStorage.getItem("userId"),
      latitude: sosNotificationLocation.value.latitude,
      longitude: sosNotificationLocation.value.longitude,
    });

    // ÎÇ¥ ÏúÑÏπòÎ°ú ÏßÄÎèÑ Ïπ¥Î©îÎùº Ïù¥Îèô
    if (map && userLocation.value.latitude && userLocation.value.longitude) {
      const currentPosition = new window.kakao.maps.LatLng(
        userLocation.value.latitude,
        userLocation.value.longitude
      );
      map.setCenter(currentPosition); // ÏßÄÎèÑ Ï§ëÏã¨ ÏÑ§Ï†ï
      map.panTo(currentPosition); // Î∂ÄÎìúÎüΩÍ≤å Ïù¥Îèô
    }
    // ÎÇ¥ ÏúÑÏπò SOS ÏÉÅÌÉúÎ°ú Í∞±Ïã†
    updateUserMarker();
  } catch (error) {
    console.error("Î™®Îì† ÏÇ¨Ïö©ÏûêÏóêÍ≤å SOS ÏïåÎ¶º Ï†ÑÏÜ° Ïã§Ìå®:", error);
  }

  sendNotification();
};

onMounted(() => {
  loadKakaoMapScript(() => {
    initializeMap(); // ÏßÄÎèÑ Ï¥àÍ∏∞Ìôî
  });

  // ÏúÑÏπò Ï∂îÏ†Å Î∞è WebSocket Ïó∞Í≤∞
  startLocationTracking();
  connectWebSocket();
});

const loadKakaoMapScript = (callback) => {
  if (window.kakao && window.kakao.maps) {
    callback();
  } else {
    const script = document.createElement('script');
    script.src = 'https://dapi.kakao.com/v2/maps/sdk.js?appkey=333bda7da18df138fb4d9b3e5cf351c4&autoload=false&libraries=clusterer,services';
    script.onload = () => {
      window.kakao.maps.load(callback);
    };
    script.onerror = () => {
      console.error("Ïπ¥Ïπ¥Ïò§ ÏßÄÎèÑ API Î°úÎìú Ïã§Ìå®");
    };
    document.head.appendChild(script);
  }
};

// ÏßÄÎèÑ Ï¥àÍ∏∞Ìôî Ìï®Ïàò
const initializeMap = async () => {
  if (mapContainer.value) {
    // ÏßÄÎèÑ Ï¥àÍ∏∞Ìôî
    map = new window.kakao.maps.Map(mapContainer.value, {
      center: new window.kakao.maps.LatLng(37.5665, 126.9780), // ÏÑúÏö∏ Ï§ëÏã¨ Ï¢åÌëú
      level: 5, // ÏßÄÎèÑ ÌôïÎåÄ Î†àÎ≤®
    });

    try {
      const response = await axios.get(`${window.location.origin}/data/Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏2.geojson`);
      const geojsonData = response.data;

      if (!geojsonData.features || geojsonData.features.length === 0) {
        console.error("GeoJSON Îç∞Ïù¥ÌÑ∞Ïóê featuresÍ∞Ä ÏóÜÏäµÎãàÎã§.");
        return;
      }

      const customMarkers = geojsonData.features.map((feature) => {
        const lat = feature.properties.YÏ¢åÌëú;
        const lng = feature.properties.XÏ¢åÌëú;

        const customMarkerContent = document.createElement("div");
        customMarkerContent.className = "custom-marker";
        customMarkerContent.innerHTML = `
          <div class="marker-icon2">
            <img src="/images/Íµ≠Í∞ÄÏßÄÏ†êÎ≤àÌò∏.png" alt="Custom Marker" />
            <div class="marker-label">${feature.properties.ÏßÄÏ†êÎ≤àÌò∏ || "Ï†ïÎ≥¥ ÏóÜÏùå"}</div>
          </div>
        `;

        const customOverlay = new window.kakao.maps.CustomOverlay({
          position: new window.kakao.maps.LatLng(lat, lng),
          content: customMarkerContent,
          xAnchor: -0.2,
          yAnchor: -0.1,
        });

        customMarkerContent.addEventListener("click", () => {
          const infoWindow = new window.kakao.maps.InfoWindow({
            content: `<div style="padding:5px;">${feature.properties.ÏßÄÏ†êÎ≤àÌò∏ || "ÏúÑÏπò Ï†ïÎ≥¥"}</div>`,
          });
          infoWindow.open(map, customOverlay);
        });

        return customOverlay; // Ïª§Ïä§ÌÖÄ ÎßàÏª§ Î∞òÌôò
      });

      // ÌÅ¥Îü¨Ïä§ÌÑ∞ ÏÉùÏÑ± Î∞è Í¥ÄÎ¶¨
      const clusterMarkers = [];
      const clusterRadiusBase = 100; // Í∏∞Î≥∏ ÌÅ¥Îü¨Ïä§ÌÑ∞ÎßÅ Î∞òÍ≤Ω(ÌîΩÏÖÄ)

      const createClusters = () => {
        clusterMarkers.forEach((cluster) => cluster.setMap(null)); // Í∏∞Ï°¥ ÌÅ¥Îü¨Ïä§ÌÑ∞ Ï†úÍ±∞
        clusterMarkers.length = 0;

        const clusters = [];
        const clusterRadius = getClusterRadius(map.getLevel()); // Ï§å Î†àÎ≤®Ïóê Îî∞Î•∏ ÌÅ¥Îü¨Ïä§ÌÑ∞ Î∞òÍ≤Ω Í≥ÑÏÇ∞

        customMarkers.forEach((marker) => {
          let addedToCluster = false;

          for (const cluster of clusters) {
            // Îëê ÏßÄÏ†ê Í∞ÑÏùò Í±∞Î¶¨ Í≥ÑÏÇ∞
            const distance = getPixelDistance(map, cluster.center, marker.getPosition());
            if (distance <= clusterRadius) { // Í≤ΩÍ≥Ñ Ï°∞Í±¥ Ìè¨Ìï®
              cluster.markers.push(marker);
              addedToCluster = true;
              break;
            }
          }

          if (!addedToCluster) {
            clusters.push({
              center: marker.getPosition(),
              markers: [marker],
            });
          }
        });

        clusters.forEach((cluster) => {
          if (cluster.markers.length > 1) {
            // ÌÅ¥Îü¨Ïä§ÌÑ∞ ÎßàÏª§ ÏÉùÏÑ±
            const clusterContent = document.createElement("div");
            clusterContent.className = "cluster-icon"; // CSS ÌÅ¥ÎûòÏä§ Ï∂îÍ∞Ä
            clusterContent.textContent = `${cluster.markers.length}`; // ÌÅ¥Îü¨Ïä§ÌÑ∞ Í∞úÏàò ÌëúÏãú

            const clusterOverlay = new window.kakao.maps.CustomOverlay({
              position: cluster.center,
              content: clusterContent,
              xAnchor: 0.5,
              yAnchor: 0.5,
            });

            clusterMarkers.push(clusterOverlay);
            clusterOverlay.setMap(map);
          } else {
            cluster.markers[0].setMap(map); // ÌÅ¥Îü¨Ïä§ÌÑ∞Ïóê Ìè¨Ìï®ÎêòÏßÄ ÏïäÏùÄ ÎßàÏª§ Í∞úÎ≥Ñ ÌëúÏãú
          }
        });
      };

      const updateMarkersVisibility = () => {
        const zoomLevel = map.getLevel();
        if (zoomLevel > 5) {
          // ÌÅ¥Îü¨Ïä§ÌÑ∞ ÌôúÏÑ±Ìôî
          customMarkers.forEach((marker) => marker.setMap(null));
          createClusters();
        } else {
          // Í∞úÎ≥Ñ ÎßàÏª§ ÌôúÏÑ±Ìôî
          clusterMarkers.forEach((cluster) => cluster.setMap(null));
          customMarkers.forEach((marker) => marker.setMap(map));
        }
      };

      const getPixelDistance = (map, pos1, pos2) => {
        const proj = map.getProjection();
        const p1 = proj.pointFromCoords(pos1);
        const p2 = proj.pointFromCoords(pos2);
        return Math.sqrt(Math.pow(p2.x - p1.x, 2) + Math.pow(p2.y - p1.y, 2));
      };

      const getClusterRadius = (zoomLevel) => {
        // Ï§å Î†àÎ≤®Ïóê Îî∞Îùº Î∞òÍ≤Ω Ï°∞Ï†ï (Ï§å Î†àÎ≤®Ïù¥ ÎÇÆÏùÑÏàòÎ°ù ÌÅ¥Îü¨Ïä§ÌÑ∞ Î∞òÍ≤ΩÏù¥ Ïª§Ïßê)
        return clusterRadiusBase * Math.pow(1, 6 - zoomLevel); // Ïòà: Ï§å Î†àÎ≤® 7->100px, Î†àÎ≤® 6->200px, ...
      };

      // Ïù¥Î≤§Ìä∏ Îì±Î°ù
      window.kakao.maps.event.addListener(map, "zoom_changed", () => {
        updateMarkersVisibility();
      });

      updateMarkersVisibility(); // Ï¥àÍ∏∞ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    } catch (error) {
      console.error("GeoJSON Îç∞Ïù¥ÌÑ∞Î•º Î°úÎìúÌï† Ïàò ÏóÜÏäµÎãàÎã§:", error);
    }
  } else {
    console.error("Map containerÍ∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
  }
};

// ÏÑúÎ≤ÑÏóêÏÑú ÏúÑÏπò Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞


// ÏúÑÏπò Ï∂îÏ†Å ÏãúÏûë
const startLocationTracking = () => {
  setInterval(async () => {
    try {
      const response = await axios.get(`http://172.30.1.53:9000/api/location`);
      const location = response.data[userId.value]; // ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÏùò ÏúÑÏπòÎßå Í∞ÄÏ†∏Ïò¥

      if (location?.latitude && location?.longitude) {
        userLocation.value.latitude = location.latitude;
        userLocation.value.longitude = location.longitude;

        console.log("ÏÇ¨Ïö©Ïûê ÏúÑÏπò ÏóÖÎç∞Ïù¥Ìä∏:", userLocation.value); // Î°úÍπÖ Ï∂îÍ∞Ä
        // ÎÇ¥ ÏúÑÏπò Í∞±Ïã†
        updateUserMarker(userId.value, location.latitude, location.longitude);
      } else {
        console.warn("ÏúÑÏπò Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§:", location);
      }
    } catch (error) {
      console.error("ÏúÑÏπò Ï†ïÎ≥¥ Í∞±Ïã† Ïã§Ìå®:", error);
    }
  }, 1000);
};



const updateUserMarker = (userId, latitude, longitude = false ) => {
  if (!map || !latitude || !longitude) return;

  const position = new window.kakao.maps.LatLng(latitude, longitude);

  const markerContent = `
    <div class="marker-icon">
       <img src="${isSOSActive.value ? '/images/SOS.png' : '/images/ÎÇ¥ÏúÑÏπò.png'}">
        <div class="wave2 ${isSOSActive.value ? '-sos-one' : '-one'}"></div>
        <div class="wave2 ${isSOSActive.value ? '-sos-two' : '-two'}"></div>
        <div class="wave2 ${isSOSActive.value ? '-sos-three' : '-three'}"></div>
    </div>`;

  if (!userMarkers[userId]) {
    userMarkers[userId] = new window.kakao.maps.CustomOverlay({
      position,
      content: markerContent,
      xAnchor: 0.5,
      yAnchor: 1,
    });
    userMarkers[userId].setMap(map);
  } else {
    userMarkers[userId].setPosition(position);
    userMarkers[userId].setContent(markerContent);
  }
   // ÏßÄÎèÑ Ï§ëÏã¨ ÏÑ§Ï†ï (ÎÇ¥ ÏúÑÏπòÎßå)
   if (userId === localStorage.getItem('userId') && autoCenterEnabled) {
    map.setCenter(position);
    autoCenterEnabled = false;
  }
};

const updateSOSMarkers = (latitude, longitude) => {
    if (!latitude || !longitude) {
        console.error("Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ SOS ÏúÑÏπò Îç∞Ïù¥ÌÑ∞ÏûÖÎãàÎã§.");
        return;
    }

    const sosPosition = new window.kakao.maps.LatLng(latitude, longitude);

    // Í∏∞Ï°¥ SOS ÎßàÏª§ ÏÇ≠Ï†ú
    sosMarkers.forEach((marker) => marker.setMap(null));
    sosMarkers = [];

    // ÏÉà SOS ÎßàÏª§ ÏÉùÏÑ±
    const sosMarkerContent = `
        <div class="marker-icon">
            <img src="/images/SOS.png">
            <div class="wave2 -sos-one"></div>
            <div class="wave2 -sos-two"></div>
            <div class="wave2 -sos-three"></div>
        </div>`;
    const sosMarker = new window.kakao.maps.CustomOverlay({
        position: sosPosition,
        content: sosMarkerContent,
        xAnchor: 0.5,
        yAnchor: 1,
    });

    sosMarker.setMap(map); // ÏßÄÎèÑÏóê SOS ÎßàÏª§ Ï∂îÍ∞Ä
    sosMarkers.push(sosMarker);

    // ÏßÄÎèÑ Ï§ëÏã¨ Ïù¥Îèô
    map.panTo(sosPosition);
};

// ÏïåÎ¶º ÌÅ¥Î¶≠ÏúºÎ°ú SOS ÎßàÏª§ Ï∂îÍ∞Ä
const handleNotificationClick = (latitude, longitude) => {
  if (!latitude || !longitude) {
    console.error("Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ SOS ÏúÑÏπò Îç∞Ïù¥ÌÑ∞ÏûÖÎãàÎã§.");
    return;
  }

  const sosPosition = new window.kakao.maps.LatLng(latitude, longitude);

  // ÎèôÏùº ÏúÑÏπòÏóê Í∏∞Ï°¥ SOS ÎßàÏª§Í∞Ä ÏûàÎäîÏßÄ ÌôïÏù∏ Î∞è Ï†úÍ±∞
  sosMarkers = sosMarkers.filter((marker) => {
    const isSamePosition = marker.getPosition().equals(sosPosition);
    if (isSamePosition) {
      marker.setMap(null); // Í∏∞Ï°¥ ÎßàÏª§Î•º ÏßÄÎèÑÏóêÏÑú Ï†úÍ±∞
    }
    return !isSamePosition; // ÎÇ®ÏïÑÏûàÎäî ÎßàÏª§Îßå ÌïÑÌÑ∞ÎßÅ
  });

  // ÏÉà SOS ÎßàÏª§ Ï∂îÍ∞Ä
  const sosMarkerContent = `
    <div class="marker-icon">
      <img src="/images/SOS.png">
      <div class="wave2 -sos-one"></div>
      <div class="wave2 -sos-two"></div>
      <div class="wave2 -sos-three"></div>
    </div>`;
  const sosMarker = new window.kakao.maps.CustomOverlay({
    position: sosPosition,
    content: sosMarkerContent,
    xAnchor: 0.5,
    yAnchor: 1,
    zIndex: 100,
  });

  sosMarker.setMap(map); // ÏßÄÎèÑÏóê SOS ÎßàÏª§ Ï∂îÍ∞Ä
  sosMarkers.push(sosMarker); // sosMarkers Î∞∞Ïó¥Ïóê Ï∂îÍ∞Ä

  // ÏßÄÎèÑ Ï§ëÏã¨ÏùÑ SOS ÏúÑÏπòÎ°ú Ïù¥Îèô
  map.panTo(sosPosition);
};


window.showLocationOnMap = (latitude, longitude) => {
  if (latitude && longitude) {
    handleNotificationClick(parseFloat(latitude), parseFloat(longitude));
  }
};

// ÏßÄÎèÑ Ï§ëÏã¨ÏùÑ ÎÇ¥ ÏúÑÏπòÎ°ú Ïù¥ÎèôÌïòÎäî Ìï®Ïàò
function Zoom() {
  if (map && userLocation.value.latitude && userLocation.value.longitude) {
    const currentPosition = new window.kakao.maps.LatLng(
      userLocation.value.latitude,
      userLocation.value.longitude
    );
    map.setCenter(currentPosition); // ÎÇ¥ ÏúÑÏπòÎ°ú Ï§ëÏã¨ Ïù¥Îèô
    autoCenterEnabled = false; // Î≤ÑÌäºÏùÑ ÎàåÎü¨ Ïù¥Îèô ÌõÑÏóêÎèÑ ÏûêÎèô Ï§ëÏã¨ÏùÄ Ïú†ÏßÄÌïòÏßÄ ÏïäÏùå
  } else {
    console.error("ÏÇ¨Ïö©Ïûê ÏúÑÏπò Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
  }
}

// Îã§Ïãú Î≥¥ÎÇ¥Í∏∞ Î≤ÑÌäº
const reSendSOS = () => {
  isSOSActive.value = true;
  handleSendNotification(); 
};

// 119 Í∞ÄÏßú Í∏∞Îä•
const callEmergency = () => {
  alert("119Ïóê Ïó∞Í≤∞ Ï§ëÏûÖÎãàÎã§. Ïû†ÏãúÎßå Í∏∞Îã§Î†§ Ï£ºÏÑ∏Ïöî."); 
};

// WebSocket Ïó∞Í≤∞ ÏÑ§Ï†ï
const connectWebSocket = () => {
  if (isConnecting || (socket && socket.readyState === WebSocket.OPEN)) return;
  isConnecting = true;
  const wsUrl = `ws://172.30.1.53:3000/ws`;

  socket = new WebSocket(wsUrl);

  socket.onopen = () => {
    console.log('WebSocket Ïó∞Í≤∞ ÏÑ±Í≥µ!');
    clearInterval(reconnectInterval);
    connectionError.value = '';
    isConnecting = false;
    if (userId.value) sendUserId(userId.value);
  };

  socket.onmessage = (event) => {
  try {
    const message = JSON.parse(event.data);

    if (message.type === "location_update") {
      const { userId, latitude, longitude } = message;

      // ÎÇ¥ ÏúÑÏπòÎßå Í∞±Ïã†
      if (userId === localStorage.getItem('userId')) {
        updateUserMarker(userId, latitude, longitude);
      }
    }

    if (message.type === "sos_notification") {
      const { userId, latitude, longitude } = message;

      // SOS ÏïåÎ¶º ÌÅ¥Î¶≠ Ïãú ÏÉÅÎåÄÎ∞© ÏúÑÏπò ÌëúÏãú
      handleNotificationClick(userId, latitude, longitude);
       // SOS ÎßàÏª§ ÏóÖÎç∞Ïù¥Ìä∏
       updateSOSMarkers(latitude, longitude);
    }
  } catch (error) {
    console.error("WebSocket Î©îÏãúÏßÄ Ï≤òÎ¶¨ Ïò§Î•ò:", error);
  }
};


  socket.onerror = (error) => {
    console.error('WebSocket ÏóêÎü¨:', error);
    isConnecting = false;
  };

  socket.onclose = () => {
    console.log('WebSocket Ïó∞Í≤∞Ïù¥ Ï¢ÖÎ£åÎêòÏóàÏäµÎãàÎã§. Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ Ï§ë...');
    connectionError.value = 'WebSocket Ïó∞Í≤∞Ïù¥ Ï¢ÖÎ£åÎêòÏóàÏäµÎãàÎã§. Ïû¨Ïó∞Í≤∞ Ï§ë...';
    isConnecting = false;
    attemptReconnect();
  };
};

// WebSocket Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ
const attemptReconnect = () => {
  if (!reconnectInterval) {
    reconnectInterval = setInterval(() => {
      connectWebSocket();
    }, 2000);
  }
};

// WebSocketÏùÑ ÌÜµÌï¥ userId Ï†ÑÏÜ°
const sendUserId = async (userId) => {
  try {
    await axios.post('/api/saveNotificationData', { userId });
  } catch (error) {
    console.error("userId Ï†ÑÏÜ° Ïã§Ìå®:", error);
  }
};

// ÏïåÎ¶º Ï†ÑÏÜ°
const sendNotification = async (isToAll = false) => {
  isModalOpenForSOS.value = true;
  try {
    const endpoint = '/api/sendNotification';
    const payload = {
      title: 'ÎπÑÏÉÅ ÏïåÎ¶º',
      body: 'ÎπÑÏÉÅ ÏïåÎ¶ºÏùÑ Î≥¥ÎÉàÏäµÎãàÎã§.',
      latitude: userLocation.value.latitude,
      longitude: userLocation.value.longitude,
      notificationType:'send'
    };

    const userId = localStorage.getItem('userId');
    if (!userId) throw new Error("ÏÇ¨Ïö©Ïûê IDÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
    payload.userId = userId;

    await axios.post(endpoint, payload, { timeout: 5000 });

    successMessage.value = isToAll ? 'Î™®Îì† ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º Ï†ÑÏÜ° ÏÑ±Í≥µ!' : 'ÏïåÎ¶º Ï†ÑÏÜ° ÏÑ±Í≥µ!';
  } catch (error) {
    console.error('ÏïåÎ¶º Ï†ÑÏÜ° Ïã§Ìå®:', error);
    connectionError.value = 'ÏïåÎ¶º Ï†ÑÏÜ° Ïã§Ìå®: ' + error.message;
  }
};


onBeforeUnmount(() => {
  if (socket) socket.close();
  clearInterval(reconnectInterval);
});

function back_site() {
  window.location.href='/mobilemainview';
}
</script>



